import React, { useEffect } from "react";

import Home from "./Home";
import About from "./About";
import Projects from "./Projects";
import "./style/layout.css";
import Contact from "./Contact";
import Aos from "aos";
import "aos/dist/aos.css";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import Rt from "./projects/profileRecipeProject/src/App";

function Layout() {
  useEffect(() => {
    Aos.init({ duration: 3000 });
  });

  useEffect(() => {});

  return (
    <Router>
      <Switch>
        <div className="pages">
          <Route exact path="/">
            <section className="pageProfile" id="home">
              <Home />
            </section>
            <section className="pageProfile" id="about">
              <About />
            </section>
            <section className="pageProfile" id="projects">
              <Projects />
            </section>
            <section id="contact">
              <Contact />
            </section>
          </Route>
          <Route path="/recipeHome">
            <Rt />
          </Route>
        </div>
      </Switch>
    </Router>
  );
}

//uninstall bootstrap to fix site css

export default Layout;
